.logo-container{
    display: flex;
    align-self: center;
    align-items: flex-start;
    justify-content: space-around;
    width: 100%;
    .page-logo{
        width: 95%;
        height: 200px;
    }
}

/* Mobile */
@media only screen and (max-width: 450px) {


}

/* Tablet Styles */
@media only screen and (min-width: 451px) and (max-width: 960px) {
    .logo-container{
        width: 100%;
        .page-logo{
            width: 80%;
            height: 200px;
        }
    }
}

/* Desktop Styles */
@media only screen and (min-width: 961px) {
    .logo-container{
        width: 80%;
        .page-logo{
            width: 75%;
            height: 200px;
        }
    }
}

@media only screen and (min-width: 1440px) {
    .logo-container{
        width: 75%;
        .page-logo{
            width: 70%;
            height: 200px;
        }
    }
}

/* 4K */
@media only screen and (min-width: 2560px) {

    .logo-container{
        width: 70%;
        .page-logo{
            width: 60%;
            height: 200px;
        }
    }

}

